import request from '@/utils/axios'


//理论水平列表
export function studyList(params) {
  return request({
    url: '/score/search',
    method: 'post',
    params
  })
}

//理论水平新增
export function add(params) {
  return request({
    url: '/score/add',
    method: 'post',
    params
  })
}

//理论删除
export function remove(params) {
  return request({
    url: '/score/remove',
    method: 'post',
    params
  })
}

//理论修改
export function modify(params) {
  return request({
    url: '/score/modify',
    method: 'post',
    params
  })
}









