<template>
  <div class="fillcontain">
    <h3>理论水平</h3>
    <div class="contain">
      <div class="table_container">
        <el-row>
          <el-col :span="24">
            <div class="readWrapper">
              <el-button
                v-if="addShow"
                class="search"
                type="primary"
                @click="modelShow('成绩录入')"
              >成绩录入</el-button>
              <el-button v-if="addShow" class="search search2">
                <a :href="href">下载模板</a>
              </el-button>
              <el-upload
                class="upload-demo"
                :action="url"
                multiple
                :on-success="handleVideoSuccess"
              >
                <el-button v-if="addShow" size="small" type="primary">上传理论考试成绩</el-button>
              </el-upload>
              <!-- <el-button class="search" type="primary" @click="modelShow('英语成绩录入')">英语成绩录入</el-button> -->
            </div>
          </el-col>
        </el-row>
        <el-row class="headerSearch">
          <el-select v-model="searchUid" @change="selectGet" filterable placeholder="请选择人员">
            <el-option
              v-for="item in optionsUser"
              v-if="item.mode !='1'"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            ></el-option>
          </el-select>
          <el-select v-model="searchType" placeholder="请选择考试类型">
            <el-option
              v-for="item in optionsType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-date-picker
            v-model="searchTime"
            type="daterange"
            value-format="timestamp"
            dateType="time"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          ></el-date-picker>
          <div class="readWrapper">
            <el-button type="primary" class="search" @click="searchBut">查询</el-button>
          </div>
        </el-row>
        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          highlight-current-row
          header-cell-class-name="table-header-class"
          style="width:100%"
        >
          <el-table-column label="序号" align="center">
            <template slot-scope="scope">
              <span>{{scope.$index+(paginations.pageIndex - 1) * paginations.pageSize + 1}} </span>
            </template>
          </el-table-column>
          <el-table-column property="name" label="姓名" align="center"></el-table-column>
          <el-table-column property="type" label="科目" :formatter="formatType" align="center"></el-table-column>
          <el-table-column property="score" label="成绩" align="center"></el-table-column>
          <el-table-column property="ks_time" label="考试日期" :formatter="formatTime" align="center"></el-table-column>
          <el-table-column fixed="right" label="操作" align="center" v-if="user.mode == 1">
            <template slot-scope="scope">
              <i @click="editClick(scope.row)" class="el-icon-edit userBut" slot="reference"></i>
              <el-popconfirm title="确定删除吗？" @onConfirm="deleteOk(scope.row)">
                <i class="el-icon-delete userBut" slot="reference"></i>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24">
            <div class="pagination">
              <el-pagination
                v-if="paginations.total > 0"
                :page-sizes="paginations.pageSizes"
                :page-size="paginations.pageSize"
                :layout="paginations.layout"
                :total="paginations.total"
                :current-page="paginations.pageIndex"
                @current-change="handleCurrentChange"
                @size-change="handleSizeChange"
              ></el-pagination>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogFormVisible"
      class="customWidth"
      id="customWidth"
    >
      <el-form :model="form">
        <el-form-item label="姓名:" :label-width="formLabelWidth">
          <el-select class="fjzt" v-model="form.uid" @change="selectGet" filterable placeholder="请选择人员">
            <el-option v-show="item.mode !='1'" v-for="item in options" :key="item._id" :label="item.name" :value="item._id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择类型:" :label-width="formLabelWidth">
          <el-select class="fjzt" v-model="form.type" placeholder="请选择考试类型">
            <el-option
              v-for="item in optionsType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="成绩:" :label-width="formLabelWidth">
          <el-input v-model="form.score" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="考试日期:" :label-width="formLabelWidth">
          <el-date-picker
            class="fjzt"
            v-model="form.ks_time"
            value-format="timestamp"
            dateType="time"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="userButModle" @click="clean">取 消</el-button>
        <el-button class="userButModle" type="primary" @click="add">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { studyList, add, remove, modify } from "@/api/study";
import { getToken } from "@/utils/auth";
export default {
  data() {
    var url =
      window.baseURL ||
      sessionStorage.getItem("serverUrl_str") +
        "/score/xls/upload?" +
        "token=" +
        getToken("Token");
    var href =
      window.baseURL ||
      sessionStorage.getItem("serverUrl_str") +
        "/成绩单模板.xlsx?" +
        getToken("Token");
    return {
      url: url,
      href: href,
      addShow: false,
      fileList: [],
      searchType: "",
      title: "航理成绩录入",
      user: this.sessionData("get", "user"),
      optionsUser: [],
      selectId: null,
      searchTime: [],
      searchUid: null,
      value: "",
      options: [],
      optionsType: [
        {
          value: 0,
          label: "航理"
        },
        {
          value: 1,
          label: "英语"
        }
      ],
      form: {
        uid: "",
        score: "",
        type: "",
        time: ""
      },
      dialogFormVisible: false,
      formLabelWidth: "120px",
      formLabelWidth2: "120px",
      tableData: [],
      editType: 0,
      loading: false,
      //需要给分页组件传的信息
      paginations: {
        total: 0, // 总数
        pageIndex: 1, // 当前位于哪页
        pageSize: 10, // 1页显示多少条
        pageSizes: [5, 10, 15, 20], //每页显示多少条
        layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
      }
    };
  },
  created() {},
  mounted() {
    this.init();
    this.init2();
    this.studyList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      uid: this.searchUid,
      type: this.searchType,
      start: this.searchTime[0],
      end: this.searchTime[1]
    });
  },
  methods: {
    init() {
      var user = this.sessionData("get", "userList");
      if (!user && !user.length) {
      } else {
        user.push({
          _id: "",
          name: "所有"
        });
        this.options = user;
        this.optionsUser = user;
      }
    },
    init2() {
      var user = this.sessionData("get", "user");
      if (user && user.mode == 1) {
        this.addShow = true;
      } else {
        this.addShow = false;
      }
    },

    clean() {
      for (var i in this.form) {
        this.form[i] = "";
      }
      this.dialogFormVisible = false;
    },

    selectGet2() {},

    searchBut() {
      this.studyList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        uid: this.searchUid,
        type: this.searchType,
        start: this.searchTime[0],
        end: this.searchTime[1]
      });
    },

    handleVideoSuccess(res) {
      if (res.code == 0) {
        this.$message({
          message: "理论学习文件上传成功！",
          type: "success"
        });
        this.studyList({
          count: this.paginations.pageSize,
          index: this.paginations.pageIndex
        });
      } else {
        this.$message({
          message: res.message,
          type: "success"
        });
      }
    },

    editClick(row) {
      var form = this.$tool.deep(row);
      this.form = form;
      this.editType = 1;
      this.title = "修改成绩";
      this.dialogFormVisible = true;
    },

    deleteOk(row) {
      remove({ _id: row._id }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "成绩删除成功！",
            type: "success"
          });
          this.studyList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex
          });
        }
      });
    },

    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.form.name = obj.name;
    },

    formatTime(row) {
      if (row.ks_time) {
        return this.$tool.formatDate(new Date(row.ks_time), 3);
      }
    },

    formatType(row) {
      return row.type ? "英语" : "航理";
    },

    modelShow(t) {
      this.title = t;
      this.editType = 0;
      this.title = "新增成绩";
      this.dialogFormVisible = true;
    },
    studyList(obj) {
      var data = this.$tool.deep(obj);
      data.index -= 1;
      studyList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          console.log("查看成绩", list);
          this.loading = false;
          this.paginations.total = list.total;
          this.tableData = list.array;
        }
      });
    },

    add() {
      if (this.editType) {
        this.title = "修改成绩";
        this.edit();
      } else {
        this.title = "新增成绩";
        this.newAdd();
      }
    },

    edit() {
      var form = this.$tool.deep(this.form);
      var data = {
        data: form,
        _id: form._id
      };
      delete data.data._id;
      modify(data).then(res => {
        if (res.code == 0) {
          this.clean();
          this.studyList();
          this.$message({
            message: this.title + "修改成功！",
            type: "success"
          });
        }
      });
    },

    newAdd() {
      if (
        !this.form.uid ||
        !this.form.score ||
        !this.form.ks_time
      ) {
        this.$message({
          message: "每一项都是必填项请将信息填写完整！",
          type: "warning"
        });
        return;
      }
      if (
        this.form.type != 0 &&
        this.form.type != 1
      ) {
        this.$message({
          message: "每一项都是必填项请将信息填写完整！",
          type: "warning"
        });
        return;
      }


      if (this.form.time && typeof this.form.time != "number") {
        this.form.time = this.form.time.getTime();
      }
      add({ data: this.form }).then(res => {
        if (res.code == 0) {
          this.clean();
          this.studyList();
          this.$message({
            message: this.title + "成功！",
            type: "success"
          });
        }
      });
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },

    handlePreview(file) {
      console.log(file);
    },

    handleClick(row) {
      console.log(row);
    },

    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },

    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.studyList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        uid: this.searchUid,
        type: this.searchType,
        start: this.searchTime[0],
        end: this.searchTime[1]
      });
    },
    // 上下分页
    handleCurrentChange(page) {
      console.log(8888888,page)
      this.paginations.pageIndex = page;
      this.studyList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        uid: this.searchUid,
        type: this.searchType,
        start: this.searchTime[0],
        end: this.searchTime[1]
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "./table.less";
/deep/.fjzt input{
  width: 380px;
}
.search2 {
  margin-left: 20px;
}
.search2 a:link {
  color: #fff;
}
/deep/.el-upload-list {
  opacity: 0;
}
/deep/.upload-demo {
  float: left;
  border-radius: 0px;
  margin-right: 20px;
  position: relative;
  top: -2px;
}
/deep/.upload-demo .el-button--primary {
  background: #04b3e0;
  border: solid 1px #03d0fb;
  border-radius: 0px;
  height: 32px;
}
.searchWrapper {
  margin-top: 6px;
}
.search {
  padding: 8px 20px !important;
  position: relative;
  top: -2px;
}
/deep/.el-dialog {
  width: 570px;
  height: 434px;
  background: url("../../assets/img/userBg.png") no-repeat;
  background-size: 570px 434px;
}
/deep/.el-dialog__body .el-form {
  width: 500px;
  margin-left: 0px;
}
/deep/#customWidth > div {
  color: #fff;
}
/deep/.el-form-item__label,
/deep/.el-dialog__title {
  color: #fff;
}
/deep/.dialog-footer {
  text-align: center;
  margin-left: 20px;
}
/deep/.search {
  background: #04b3e0;
  border: solid 1px #03d0fb;
  border-radius: 0px;
}
.userButModle {
  width: 126px;
  height: 41px;
  color: #fff;
  font-size: 16px;
  border: none;
  background: url("../../assets/img/but.png") no-repeat;
}
.userButModle:first-child {
  margin-left: 0px;
  margin-top: 0px;
}
.userBut {
  font-size: 18px;
  color: #02c3c3;
  margin: 0 10px;
}
.headerSearch > div {
  float: left;
}
.fillcontain > h3 {
  color: #52e5ff;
  font-size: 20px;
  font-weight: 500;
  position: relative;
  top: -8px;
  width: 82px;
  left: 60px;
}
.fillcontain {
  padding-bottom: 0;
  margin-left: 16px;
  height: 984px;
  background: url("../../assets/img/bbg.png") no-repeat;
}
.fillcontain {
  padding-bottom: 0;
}
@media screen and (max-width: 1366px) {
  .fillcontain {
    background: url("../../assets/img/cbg.png") no-repeat 0px 0px;
    background-size: 98% 100%;
    width: 100%;
  }
  .fillcontain > h3 {
    left: 26px;
  }
}
.contain {
  padding: 10px;
  margin-bottom: 20px;
}
.pagination {
  padding: 10px 20px;
  text-align: right;
}
.readWrapper {
  height: 50px;
}
.customWidth {
  width: 50%;
  left: 23%;
}
</style>



